<template>
  <div class="mobile-container">
    <Header/>

    <div class="m-content">
      <div class="m-con">
        <div class="m-head">
          <div class="m-item">
            <div class="item-lebel">{{ transData.input }} GKC</div>
            <p>{{ $t('lang.trans_detail1') }}</p>
          </div>
          <div class="m-item">
            <div class="item-lebel">{{ transData.output }} GKC</div>
            <p>{{ $t('lang.trans_detail2') }}</p>
          </div>
          <div class="m-item">
            <div class="item-lebel">{{ transData.fees }} GKC</div>
            <p>{{ $t('lang.trans_detail3') }}</p>
          </div>
        </div>
        <div class="head-btn">
          <div class="to-details" @click="handleHidden">{{ $t('lang.trans_detail_button') }}</div>
        </div>
        <div class="block-con" v-show="txinfo">
          <div class="block-item">
            <label for="">{{ $t('lang.trans_detail4') }}：</label>
            <p>{{ transData.size }}</p>
          </div>
          <div class="block-item">
            <label for="">{{ $t('lang.trans_detail5') }}：</label>
            <p>{{ transData.height }}</p>
          </div>
          <div class="block-item">
            <label for="">{{ $t('lang.trans_detail6') }}</label>
            <p>{{ filterDate(transData.timestamp) }}</p>
          </div>
          <div class="block-item" v-if="raw.type === 'Call' || raw.type === 'Create'">
            <label for="">{{ $t('lang.trans_detail7') }}</label>
            <p>
              <router-link tag="a" :to="{path:'/tokendetail',query:{txid: transData.hash }}">{{ $t('lang.trans_detail8')
                }}
              </router-link>
            </p>
          </div>
        </div>
      </div>
      <div class="m-tips">{{ $t('lang.trans_trans_title') }}</div>
      <div class="m-con m-bg">
        <div class="m-con-log">
          <div class="log-title">
            <img src="@/assets/img/exchange.png" alt="">
            <div class="log-link">
              <router-link tag="a" :to="{path:'/mtx',query:{txid: transData.hash }}">{{ transData.hash }}</router-link>
            </div>
          </div>
          <div class="log-con">
            <div class="log-con-title">{{ $t('lang.trans_trans1') }}</div>
            <div class="log-con-info">
              <div class="info-item" v-for="(item, index) in transInput" :key="index">
                <div class="item-child">
                  <div class="item-form"><span>{{ item.amount }} GKC</span> From</div>
                  <div class="item-details">
                    <img src="@/assets/img/qr.png" alt="">
                    <router-link v-if="isGoto(item.address)" tag="a" :to="{path:'/maddress',query:{address: item.address}}">{{ item.address }}
                    </router-link>
                    <a v-else href="javascript:void(0)">{{ item.address }} </a>
<!--                    <router-link tag="a" :to="{path:'/maddress',query:{address: item.address}}">{{ item.address }}-->
<!--                    </router-link>-->
                  </div>
                </div>
              </div>
              <div class="log-con-img"><img src="@/assets/m/down.png" alt=""></div>
              <div class="info-item" v-for="(item, index) in transOutput" :key="index">
                <div class="item-child">
                  <div class="item-form" v-if="item.address === 'unknown'"><span>Empty</span></div>
                  <div class="item-form"
                       v-else-if="item.address === '0x20 0xc87b1c605f7e14a848ff5c2cb4805446ce4c3bf71da1829dffd05117a977f09b 0x20 0x21b463e3b52f6201c0ad6c991be0485b6ef8c092e64583ffa655cc1b171fe856 0xc'">
                    <span>contract</span></div>
                  <div class="item-form" v-else><span>{{ item.amount }} GKC</span> To</div>
                  <div class="item-details" v-if="item.address === 'unknown'">
                    <img src="@/assets/img/unkown.png" alt="">
                    <p style="display: inline-block">Coinbase</p>
                  </div>
                  <div class="item-details"
                       v-else-if="item.address === '0x20 0xc87b1c605f7e14a848ff5c2cb4805446ce4c3bf71da1829dffd05117a977f09b 0x20 0x21b463e3b52f6201c0ad6c991be0485b6ef8c092e64583ffa655cc1b171fe856 0xc'">
                    <img src="@/assets/img/unkown.png" alt="">
                    <p style="display: inline-block">Non-standard script</p>
                  </div>
                  <div class="item-details" v-else>
                    <img src="@/assets/img/qr.png" alt="">

                    <router-link v-if="isGoto(item.address)" tag="a" :to="{path:'/maddress',query:{address: item.address}}">{{ item.address }}
                    </router-link>
                    <a v-else href="javascript:void(0)">{{ item.address }}
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Header from '@/components/MHeader'
  import Footer from '@/components/MFooter'
  import pageSize from '@/components/MPage'
  import QRCode from 'qrcodejs2'
  import dateFns from 'date-fns'

  export default {
    components: {
      Header,
      Footer,
      pageSize
    },
    data() {
      return {
        transInput: [],
        transOutput: [],
        transType: 'first',
        transData: [],
        txid: '',
        txinfo: false,
        raw: []
      }
    },
    created() {
      this.txid = this.$route.query.txid;
    },
    watch: {
      $route() {
        this.txid = this.$route.query.txid;
        this.getTransInfo(this.txid)
      }
    },
    computed: {},
    mounted() {
      this.getTransInfo(this.txid)
    },
    methods: {
      getTransInfo(txid) {
        this.$http.post("api/block/trans", {txid: txid}).then(res => {
          if (res.data.code === 200) {
            this.transData = res.data.data.trans
            if (res.data.data.input) {
              this.transInput = []
              for (let index in res.data.data.input) {
                res.data.data.input[index].sort = parseInt(index) + 1
                this.transInput.push(res.data.data.input[index])
              }
            }
            if (res.data.data.output) {
              this.transOutput = []
              for (let index in res.data.data.output) {
                res.data.data.output[index].sort = parseInt(index) + 1
                this.transOutput.push(res.data.data.output[index])
              }
            }
            this.raw = res.data.data.raw
            setTimeout(() => {
              $(".qrdata").html("")
              // for (const index in this.transInput ) {
              //     this.qrcode(20, 20, this.transInput[index].address + index, this.transInput[index].address)
              // }
              // for (const index2 in this.transOutput ) {
              //     this.qrcode(20, 20, this.transOutput[index2].address + index2, this.transOutput[index2].address)
              // }
            }, 500);
          }
        })
      },
      handleHidden() {
        if (!this.txinfo) {
          this.txinfo = true
        } else {
          this.txinfo = false
        }
      },
      qrcode(width, height, id, address) {
        new QRCode(id, {
          text: address,
          width: width,
          height: height,
          colorDark: '#333333',
          colorLight: '#ffffff',
          correctLevel: QRCode.CorrectLevel.L
        })
      },
      filterDate(value, type) {
        if (!value) return ''
        if (value > 0) {
          value = value * 1000
        }
        const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
        return dateFns.format(value, filterType)
      },
      isGoto(str) {
        if (str.indexOf("G") === 0 && str.length === 34){
          return true;
        }else{
          return false;
        }
      }
    }
  }


</script>

<style scoped>
  .m-pos-bg {
    text-align: left;
    position: relative;
    margin-bottom: .8rem;
  }

  .m-pos-bg .bg-img {
    width: 100%;
    height: 8rem;
  }

  .m-pos-con {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    color: #ffffff;
  }

  .pos-con-img {
    margin: 1rem;
    display: flex;
    align-items: center;
  }

  .pos-con-img img {
    width: 2rem;
    height: 2rem;
    margin-right: .8rem;
  }

  .m-nodeid {
    word-wrap: break-word;
    margin: 0 5rem 0 3rem;
  }

  .m-btn-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
  }

  .m-btn {
    width: 100px;
    font-size: 12px;
    text-align: center;
    background: #F0F0F0;
    line-height: 32px;
    border-radius: 4px;
  }

  .m-content {
    padding: 1rem;
    background: #E6E9EE;
  }

  .m-head {
    background: #ffffff;
  }

  .m-head .m-item {
    text-align: center;
    border-bottom: 1px solid #F0F0F0;
    padding: 1rem 0;
  }

  .m-item p {
    color: #888888;
    font-size: 12px;
  }

  .item-lebel {
    font-size: 17px;
    margin-bottom: .5rem;
  }

  .m-tips {
    padding: .7rem;
    text-align: left;
  }

  .m-con {
    background: #ffffff;
  }

  .head-btn {
    text-align: center;
    padding: 1.5rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .head-btn .to-details {
    width: 10rem;
    line-height: 3rem;
    background: rgba(0, 93, 166, 1);
    opacity: 1;
    border-radius: 4px;
    color: #ffffff;
  }
</style>



